<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import Upload from "@/components/Upload/Upload.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "consentUpload-component",
  components: { Upload, TableComponent, Pagination1 },
  data() {
    return {
      form: {
        add4Apply: [], // 未上傳同意書清單
        cstPeople: [], // 同意書上傳人員
      },
      rules: {
        add4Apply: [{ required: true, message: "必填", trigger: "change" }],
      },

      cstApplierTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "create_date", label: "申請日期", width: "140" },
        { id: "personInfo", label: "姓名(性別)身分證字號", width: "200" },
        { id: "file_Name", label: "附檔", width: "" },
        { id: "approve_Status2", label: "審核狀態", width: "140" },
        { id: "reject_Reason", label: "備註", width: "200" },
        { id: "name", label: "審核人員", width: "125" },
      ],
    };
  },
  computed: {
    cstChoiceList() {
      return this.$store.state.cst_choicelist;
    },
    // 個人同意書申請資料 (All)
    cstApplier() {
      return this.$store.state.cst_applier;
    },
    // 個人同意書申請資料 (單頁)
    cstApplierList() {
      return this.$store.state.cst_applierlist;
    },
  },
  methods: {
    // 將所選的人員代入表格中
    add4ApplyHandler() {
      this.form.cstPeople = [];
      for (let i = 0; i < this.form.add4Apply.length; i++) {
        let item = this.cstChoiceList.filter(
          (e) => e.index == this.form.add4Apply[i]
        )[0];
        this.form.cstPeople.push(item);
      }
    },

    async cstFileChgHandler(file, index) {
      let raw = file[0].raw;
      let base64File = await this.toBase64(raw);

      // 取得檔名及 base64 轉檔，並移除欄位驗證
      this.form.cstPeople[index].fileName = file[0].name;
      this.form.cstPeople[index].fileBase64 = base64File;
      this.$refs.form.clearValidate("cstPeople." + index + ".fileName");
    },
    cstFileRmvHandler(file, index) {
      this.form.cstPeople[index].fileName = "";
      this.form.cstPeople[index].fileBase64 = "";
      this.$refs.form.validateField("cstPeople." + index + ".fileName");
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.cstPeople = [];
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch(
              "addCstApplier",
              this.form.cstPeople
            );
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "申請成功",
              });

              // 清除表單，並更新申請列表 (呈現第一頁)
              this.resetForm(formName);
              await this.$store.dispatch("getCstApplier");
              this.$store.commit("CURRENT_PAGE_INIT");
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },

    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getPrivacyZone,
        });
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${item.file_Name}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "cst_applier",
        paramsListName: "cst_applierlist",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取未上傳同意書人員、同意書申請資料
    try {
      await this.$store.dispatch("getCstForChoice");
      await this.$store.dispatch("getCstApplier");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./consentUpload.html"></template>
